import api from '../http'

export const getGoodsTypeList = () => {
	return api.http({
		url:'/shop/goods-type/list/all',
		method:'get'
	})
}

export const getGoodsListForPage = (queryForm) => {
	return api.http({
		url:'/shop/goods/list/page',
		method:'get',
		params:queryForm
	})
}

export const findGoodsById = (id) => {
	return api.http({
		url:`/shop/goods/find/id/${id}`,
		method:'get',
		data:id
	})
}

export const getHotGoodsList = () => {
	return api.http({
		url:'/shop/goods/list/hot',
		method:'get'
	})
}