<template>
	<div>
		<van-nav-bar
		  title="首页"
		/>
		<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
		  <van-swipe-item v-for="item in imgList" :key="item">
				<img :src="item" class="banner" alt="">
			</van-swipe-item>
		</van-swipe>
		<div class="tool-bar">
			<div class="container">
				<div class="item" @click="jump('/shop')">
					<div class="icon">
						<van-icon name="logistics"></van-icon>
					</div>
					<div class="text">
						商城
					</div>
				</div>
				<div class="item" @click="jump('/activity')">
					<div class="icon">
						<van-icon name="discount"></van-icon>
					</div>
					<div class="text">
						活动
					</div>
				</div>
				<div class="item" @click="jump('/me')">
