					</div>
					<div class="text">
						我的
					</div>
				</div>
			</div>
		</div>
		<div class="boxs">
			<div class="item-box">
				<div class="item-title">
					<van-cell is-link="" value="查看所有商品" @click="jump('/shop')"  >
						<template #title>
							<van-icon name="gift-o"></van-icon>
							热门商品
						</template>
					</van-cell>
					
				</div>
				<div class="item-content">
					<van-card
						v-for="item in hotGoodsList"
						:key="item.id"
					  :num="item.count"
						tag="hot"
					  :price="item.price*item.discount/10"
					  :desc="item.description"
					  :title="item.name"
					  :thumb="item.logo"
					  :origin-price="item.price"
						
						@click="handleGoodsInfo(item)"
					>
						<template #tags>
							<div>
								销量：{{item.saleCount}}
							</div>
						</template>
					</van-card>
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import p1 from '@/static/p1.jpeg'
	import p2 from '@/static/p2.jpeg'
	import p3 from '@/static/p3.jpeg'
	import p4 from '@/static/p4.jpeg'
	import p5 from '@/static/p5.jpeg'
	import {mapState,mapMutations} from 'vuex'
	import {getHotGoodsList} from '@/api/shop-api.js'
	export default{
		data(){
			return {
				disabled:false,
				imgList:[p1,p2,p3,p4,p5],
				hotGoodsList:[]
			}
		},
		async created(){
			await this.getHotGoodsList()
		},
		methods:{
			...mapMutations(['setActive']),
			handleOnEnd(){
				console.log('end')
				if(this.disabled == false){
					this.disabled = true
				}
				
				setTimeout(() => {
					this.disabled = false
				},1000)
			},
			jump(path){
				this.setActive(path.substring(1))
				// this.$router.push(path)
			},
			async getHotGoodsList(){
				let res = await getHotGoodsList();
				if(res.data.code == 200){
					this.hotGoodsList = res.data.data.list
				}
			},
			handleGoodsInfo(item){
				this.$router.push({path:'/shop-info',query:{id:item.id}})
			}
		}
	}
</script>

